import { Component, h, Prop, Element } from '@stencil/core';

@Component({
	tag: 'ks-card'
})
export class Card {

    @Prop() padding: boolean = false;
    @Prop() small: boolean = false;
    @Prop() margin: boolean = false;
    @Prop() height: boolean = false;


    @Element() root: HTMLElement;

    componentDidLoad() { 
        this.root.style.display = "block";
        
        if(this.height)
            this.root.style.height = "100%";
	}

	render() {
        const padding: string = this.padding ? this.small ? "uk-padding-small" : "" : "uk-padding-remove";
        const margin: string = this.margin ? "uk-margin-bottom" : "";
        const height: string = this.height ? "uk-height-1-1" : "";

		return(
            <div class={"uk-card uk-card-default uk-card-body " + padding + " " + margin + " " + height}> 
                <slot />
            </div>
		);
	}
}
